.Card {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 6px 10px;
  margin-bottom: 10px; }
  .Card:last-child {
    margin-bottom: 0; }
  .Card-header {
    padding: 4px;
    margin-bottom: 14px;
    border-bottom: 1px solid #eee;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
    .Card-header h4 {
      margin: 0;
      padding: 0;
      text-transform: uppercase;
      font-size: 13px;
      letter-spacing: 0.5px;
      color: #aaa;
      font-weight: 400; }

.cover-canvas-wrapper .cover-canvas {
  width: 100%;
  padding: 15px; }

.cover-canvas-wrapper .cover-img {
  display: none; }

.cover-canvas-wrapper .weather-img {
  display: none; }

.avatar-edit {
  position: absolute;
  right: 12px;
  z-index: 1;
  top: 10px; }
  .avatar-edit input {
    display: none; }
    .avatar-edit input + label {
      display: inline-block;
      width: 34px;
      height: 34px;
      margin-bottom: 0;
      border-radius: 100%;
      background: #FFFFFF;
      border: 1px solid transparent;
      -webkit-box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12);
      box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12);
      cursor: pointer;
      font-weight: normal;
      -webkit-transition: all .2s ease-in-out;
      -o-transition: all .2s ease-in-out;
      transition: all .2s ease-in-out; }
      .avatar-edit input + label:hover {
        background: #f1f1f1;
        border-color: #d6d6d6; }
      .avatar-edit input + label:after {
        content: '';
        background: url("./assets/images/edit.svg") no-repeat center;
        background-size: 17px;
        color: #757575;
        position: absolute;
        width: 32px;
        height: 32px; }



